import React from "react";
import Skeleton from "@material-ui/lab/Skeleton";

const PreProfile = () => {
    return (
        <>
            <div className="profile-loading-wrapper">
                <Skeleton
                    className="mb"
                    animation="wave"
                    variant="circle"
                    width={160}
                    height={160}
                />
                <Skeleton
                    className="mb"
                    animation="wave"
                    variant="h1"
                    width={400}
                    height={30}
                />

                <Skeleton
                    animation="wave"
                    variant="rect"
                    width={510}
                    height={100}
                />
            </div>
        </>
    );
};

export default PreProfile;
