import React from 'react'
import Skeleton from "@material-ui/lab/Skeleton";

const PrePosts = () => {
    return (
        <>
             <div className="posts-loading-wrapper">
                        <div>
                            <Skeleton
                                className="mb"
                                animation="wave"
                                variant="circle"
                                width={50}
                                height={50}
                            />
                            <Skeleton
                                className="mb"
                                animation="wave"
                                variant="h1"
                                width={400}
                                height={30}
                            />
                        </div>
                        <Skeleton
                            animation="wave"
                            variant="rect"
                            width={510}
                            height={130}
                            className="mb"
                        />
                        <Skeleton
                            animation="wave"
                            variant="rect"
                            width={510}
                            height={50}
                        />
                    </div>
        </>
    )
}

export default PrePosts
